@import "./../../styles/variables.scss";

@font-face {
  font-family: "southbank";
  src: url("../../assets/fonts/southbanklt-webfont.woff") format("woff");
}

.container {
  position: relative;
  height: 100vh;
  min-height: 800px;
  margin: 0 auto;
}

.image {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.hero {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(#0a192f, 0.2);
  padding-top: $headerHeight;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: "Montserrat", sans-serif;

  &__content {
    margin: 0 1.5rem;
    background: rgba($dark, 0.6);
    backdrop-filter: blur(2px);
    padding: $spacing-lg;
    border-radius: $borderRadius;
    width: 1000px;
    display: flex;
    text-align: left;
    flex-direction: column;
    letter-spacing: $letterSpacing;

    @include breakpoint(md) {
      text-align: center;
    }

    &__title {
      font-size: 2.8rem;
      color: $white;
      font-weight: 700;
      line-height: 2.9rem;
      margin-bottom: $spacing-md;
      text-align: start;

      @include breakpoint(sm) {
        font-size: 3rem;
      }

      @include breakpoint(md) {
        font-size: 3.8rem;
        text-align: center;
      }
    }

    &__tagline {
      font-size: 1.3rem;
      line-height: 26px;
      font-weight: 700;
      color: $light;
      font-style: italic;
      margin: $spacing-sm 0 $spacing-lg;

      @include breakpoint(sm) {
        font-size: 1.8rem;
      }

      @include breakpoint(md) {
        font-size: 1.65rem;
      }
    }

    &__description {
      text-transform: capitalize;
      color: $light;
      width: 100%;
      font-size: 1rem;
      margin-bottom: $spacing-lg;
      line-height: 22px;

      @include breakpoint(sm) {
        width: 80%;
        font-size: 1.1rem;
      }

      @include breakpoint(md) {
        width: 60%;
        margin: 0 auto $spacing-lg auto;
      }
    }

    &__cta {
      display: flex;
      flex-direction: column;
      align-items: flex-start;

      button,
      a {
        width: 100%;
      }

      button:not(:last-child) {
        margin-bottom: $spacing-sm;
      }

      @include breakpoint(md) {
        align-items: center;

        button,
        a {
          width: auto;
        }

        margin: 0 auto;
        flex-direction: row;

        button:not(:last-child) {
          margin-bottom: 0;
          margin-right: $spacing-sm;
        }
      }
    }
  }
}
